#select_job {
  height: calc(100vh - 50px);
  margin-top: 50px;
}

.job_card {
  width: 490px;
  height: 120px;
  background-color: blue;
  margin: auto;
  border-radius: 5px;
  opacity: .7;
  overflow: hidden;
  position: relative;
}

.job_card:hover {
  cursor: pointer;
}

.job_card:hover img {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

.job_card img {
  width: 490px;
  position: absolute;
  top: -80px;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

.job_card:nth-of-type(4) img {
  top: -15px;
}

.job_card_img_cover {
  width: 100%;
  height: 100%;
  opacity: .7;
  position: absolute;
}

.job_introduce {
  position: absolute;
  color: #fff;
  padding: 10px;
  z-index: 3;
}

.job_introduce_title {
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 20px;
}

.job_introduce_content {
  font-size: 13px;
  line-height: 20px;
}

.job_card_creator {
  background-color: rgb(185, 79, 85) !important;
}

.job_card_video_editor {
  background-color: rgb(5, 130, 247) !important;
}

.job_card_others {
  background-color: rgb(159, 125, 74) !important;
}

.job_card_selected {
  width: 300px;
  height: 5px;
  background-color: #ddd;
  margin: auto;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  position: relative;
}

.job_card_wrapper {
  width: 100%;
  height: 100%;
  z-index: 9999;
  position: absolute;
}

.focus_border {
  width: 0;
  height: 100%;
  border-radius: 5px;
  position: absolute;
  bottom: 0;
  left: 50%;
  -webkit-transition: .4s;
  -o-transition: .4s;
  transition: .4s;
}

.focused {
  width: 100%;
  -webkit-transition: .4s;
  -o-transition: .4s;
  transition: .4s;
  left: 0;
}


