.init_task_complete_btn {
  width: 100px;
  height: 40px;
  margin: auto;
  margin-top: 50px;
  position: relative;
  background-color: rgb(185, 79, 85);
  opacity: .4;
  border-radius: 5px;
  -webkit-transition: .4s;
  -o-transition: .4s;
  transition: .4s;
}

.init_task_complete_btn span {
  color: #fff;
  font-size: 12px;
}

.init_task_complete {
  opacity: 1 !important;
  -webkit-transition: .4s;
  -o-transition: .4s;
  transition: .4s;
}

.init_task_complete:hover {
  cursor: pointer;
}


